import React, { useMemo } from 'react'
import PropTypes from 'prop-types'
import Container from '@material-ui/core/Container'
import SectionHeader from '../../layout/sections-header/section-header'
import BlogCategories from '../blog-categories/blog-categories'
import BlogList from '../blog-list/blog-list'
import withStyles from './blog-page.style'

const BlogPage = ({ classes, data }) => {
  const [selected, setSelected] = React.useState('')

  const categories = data.categories.nodes
  const content = data.content.nodes
  let filteredContent = content

  if (selected !== '') {
    filteredContent = content.filter(
      (item) => item.categories[0]['slug'] === selected
    )
  }

  const filterHandler = (filter) => {
    setSelected(filter)
  }

  return (
    <Container className={classes.root}>
      <div className={classes.header}>
        <SectionHeader title="Blog" />
      </div>

      {categories.length > 1 && (
        <BlogCategories
          data={categories}
          selected={selected}
          onSelected={filterHandler}
        />
      )}
      <BlogList data={filteredContent} />
    </Container>
  )
}

BlogPage.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
}

export default withStyles(BlogPage)
