import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'
import Grid from '@material-ui/core/Grid'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import CardActions from '@material-ui/core/CardActions'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import { TrimWpExcerpt } from '../../../lib/trim-wp-excerpt'
import withStyles from './blog-list.style'

const BlogList = ({ classes, data }) => {
  return (
    <Grid container spacing={4} className={classes.root}>
      {data.map(item => (
        <Grid key={item['slug']} item xs={12}>
          <Card raised={true}>
            <CardContent style={{ flexGrow: 1 }}>
              <Typography
                component="h2"
                variant="h5"
                dangerouslySetInnerHTML={{ __html: item['title'] }}
              />
              <Typography
                noWrap={true}
                variant="subtitle1"
                color="textSecondary"
              >
                {item['date']}
              </Typography>
              <Typography
                gutterBottom
                variant={'body1'}
                align={'left'}
                className={classes.desc}
                dangerouslySetInnerHTML={{
                  __html: TrimWpExcerpt(item['excerpt']),
                }}
              />
            </CardContent>
            <CardActions className={classes.actions} disableSpacing>
              <Button
                component={Link}
                to={`/blog/${item['date']}/${item['slug']}`}
                variant="contained"
                color="primary"
                className={classes.link}
              >
                Weiterlesen
              </Button>
            </CardActions>
          </Card>
        </Grid>
      ))}
    </Grid>
  )
}

BlogList.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.array.isRequired,
}

export default withStyles(BlogList)
