import React from 'react'
import PropTypes from 'prop-types'
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
import Chip from '@material-ui/core/Chip'
import withStyles from './blog-categories.style'

const BlogCategories = ({ classes, data, selected, onSelected }) => {
  const clickHandler = cat => () => {
    onSelected(cat !== selected ? cat : '')
  }

  return (
    <Container className={classes.root}>
      <Grid container justify={'center'} spacing={2}>
        {data.map(item => (
          <Grid key={item['slug']} item>
            <Chip
              label={item['name']}
              color={selected === item['slug'] ? 'primary' : 'default'}
              onClick={clickHandler(item['slug'])}
            />
          </Grid>
        ))}
      </Grid>
      <br />
    </Container>
  )
}

BlogCategories.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.array.isRequired,
  selected: PropTypes.string.isRequired,
  onSelected: PropTypes.func.isRequired,
}

export default withStyles(BlogCategories)
