import React from 'react'
import { graphql } from 'gatsby'
import Layout from '../components/layout/layout'
import SEO from '../components/seo/seo'
import BlogPage from '../components/blog/blog-page/blog-page'

const Blog = ({ data }) => (
  <Layout>
    <SEO title="Blog" />
    <BlogPage data={data} />
  </Layout>
)

export const query = graphql`
  query {
    categories: allWordpressCategory {
      nodes {
        name
        slug
      }
    }
    content: allWordpressPost {
      nodes {
        id
        date(formatString: "MM/DD/YYYY")
        title
        slug
        author
        excerpt
        categories {
          name
          slug
        }
      }
    }
  }
`

export default Blog
